.App {
  background-color: #282c34;
  // min-height: 100vh;
  display: flex;
  // flex-direction: column;
  // align-itvws: center;
  justify-content: center;
  font-size: 2vmin;
  color: white;
  // text-align: center;
}

.container {
  align-itvws: flex-start;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  margin: 20px auto;
  max-width: 1440px;
}

.flex {
  display: flex;
}

.align-center {
  align-itvws: center;
}

.justify-center {
  justify-content: center;
}

.flex-column {
  flex-direction: column;
}

.border {
  border: 1px solid #ffa601;
}

.text {
  width: 40vw;
}

h1,
h2,
h3 {
  padding: 10px;
}

h1 {
  background-color: #ffa601;
  color: #383838;
  font-size: 3vw;
  font-weight: bold;
}

p {
  margin: 0;
  padding: 10px;
}

button {
  background-color: #ffa601;
  border: none;
  border-radius: 5px;
  color: #282c34;
  cursor: pointer;
  // font-size: 1.5vw;
  font-weight: bold;
  margin: 10px;
  padding: 10px;
  // text-transform: uppercase;
  width: -moz-fit-content;
  width: fit-content;
}

input {
  background-color: #282c34;
  border: 1px solid #ffffff;
  border-radius: 5px;
  color: #ffa601;
  font-size: 1vw;
  font-weight: bold;
  margin: 10px;
  padding: 10px;
  text-transform: uppercase;
}

.hightlight {
  background-color: #ffa601;
  color: #282c34;

  // font-size: 1vw;
  font-weight: bold;

  width: -moz-fit-content;
  width: fit-content;

  display: inline;
  margin: 10px;
}

@media screen and (max-width: 960px) {
  .flex {
    display: inline-block;
  }

  .container {
    margin: 20px;
  }
}
